<template>
  <v-form v-model="valid" @submit="search">
    <v-container>
      <v-row>
        <v-col cols="6">
          <v-combobox
              v-model="partner"
              :items="partners"
              item-text="Name"
              item-value="Id"
              label="Партнер"
              multiple
              hint="Выберите партнеров для поиска"
          ></v-combobox>
        </v-col>

        <v-col cols="6">
          <v-combobox
              v-model="cession"
              :disabled="partner.length === 0"
              :items="filteredCessions"
              item-text="Name"
              item-value="Id"
              :label="partner.length === 0 ? 'Выберите партнеров' : 'Цессии' "
              multiple
              no-data-text="Не найдено цессий для данных партнеров"
              hint="Цессии"
              :persistent-hint="true"
          ></v-combobox>
        </v-col>
      </v-row>
      <v-row justify="space-between">
        <v-row>
          <v-col>
            <v-btn-toggle
                v-model="parameter"
                rounded
            >
              <v-btn v-for="(text, key) of mapParameters" :value="key">
                {{ text }}
              </v-btn>
            </v-btn-toggle>
            <p>! - строгое соответствие,  ? - не строгое соответствие</p>
          </v-col>
        </v-row>
        <v-btn-toggle
            v-model="scans"
            rounded
        >
          <v-btn v-for="(text, key) of mapScans" :value="key">
            {{ text }}
          </v-btn>
        </v-btn-toggle>
      </v-row>
      <v-row>
        <v-textarea
            v-model="searchParam"
            :disabled="parameter === undefined"
            :auto-grow="true"
            :clearable="true"
            :filled="true"
            hint="Значения для поиска"
            :persistent-hint="true"
            :label="parameter === undefined ? 'Выберите тип параметра':'Введите значения' "
            :no-resize="true"
            :single-line="true"
            @keyup.enter.native.prevent="search"
            @keyup.down="searchParam += '\n'"
        ></v-textarea>
      </v-row>

      <v-row>
        <div class="flex-grow-1" />
        <v-btn class="mx-2" @click="clearSearch">
          Сбросить
        </v-btn>
        <v-btn type="submit" :disabled="action">
          Поиск
        </v-btn>
      </v-row>
    </v-container>
    <FuckDialog v-model="dialog"/>
  </v-form>
</template>
<script>
import { mapState, mapActions } from "vuex" // , mapGetters,
import FuckDialog from "../../components/FuckDialog"

const mapScans = {
  scans: "Со сканами",
  allscans: "Все",
  noscans: "Без сканов"
}

const mapParameters = {
  idpristav: "ID пристав",
  idpkb: "ID ПКБ",
  idhkd: "ID ХКД",
  fio: "ФИО!",
  contractnumber: "Номер договора!",
  fiolite: "ФИО?",
  contractnumberlite: "Номер договора?"
}

export default {
  components: {
    FuckDialog
  },
  props: ["status"],
  data: ()=> ({
    dialog: false,
    action: false,
    mapScans,
    mapParameters,
    valid: undefined,
    partner: [],
    cession: [],
    parameter: "idpristav",
    searchParam: "",
    scans: "allscans"
  }),
  mounted() {
    const params = {
      idDebt: 12
    }
    Promise.all([
      // this.GET_DEBT_CARD(params)
      this.GetPartners(params),

    // Cessions: []
    // Id: 1
    // Name: "AJP Credit Holdings"
    // PartnerTemplates: []

      this.GetCessions(params)

    // CessionScans: []
    // CommitDate: null
    // Contracts: []
    // Date: "2012-09-28T00:00:00"
    // DateLastUpdate: "2019-07-14T11:12:54.45"
    // Id: 1
    // Name: "CH-08/2012"
    // NewPrefix: null
    // Partner: null
    // PartnerId: 1

    ])
  },
  computed: {
    ...mapState("partners", {statusP: "status",
                              error: "error",
                              partners: "items"}),
    ...mapState("cessions", {statusC: "status",
                            error: "error",
                            cessions: "items"}),
    filteredCessions() {
      const idPartners = this.partner.map(item=>item.Id)
      return this.cessions.filter(item=>item.PartnerId in idPartners)
    }
  },
  methods: {
    ...mapActions("partners", {GetPartners: "GET_ITEMS"}),
    ...mapActions("cessions", {GetCessions: "GET_ITEMS"}),
    clearSearch() {
      this.partner = []
      this.cession = []
      this.parameter = "idpristav"
      this.searchParam = ""
      this.scans = "allscans"
    },
    search() {
      const object = {
        partner: this.partner,
        cession: this.cession
      }
      object[this.parameter] = this.searchParam
      object.scans = this.scans
      this.$emit("search", object)
      this.dialog = true
      this.action = true
    }
  }
}
</script>
