<template>
  <div>
  <v-dialog
      v-model="dialog_local"
      @click:outside="outClick"
      width="500"
  >
    <v-card>
      <v-card-title
          class="headline grey lighten-2"
          primary-title
      >
        Подтвердите действие
      </v-card-title>

      <v-card-text>
        {{ textDialog }}
      </v-card-text>

      <v-divider></v-divider>

      <v-card-actions>
        <div class="flex-grow-1"></div>
        <v-btn
            color="primary"
            text
            @click="resetDialog"
        >
          Ок
        </v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  </div>
</template>
<script>
const texts = [
  "Вы точно уверены что хотите начать поиск?",
  "Нет, вы как то не очень уверенно говорите что вы уверены.",
  "Ну так что, мы поиск начинаем?",
  "Точно начинаем? ощущение что это мне надо а не вам",
  "Вот щас ваше нажатие выглядело как одолжение",
  "Ты просишь меня начать поиск, но ты делаешь это без уважения",
  "И вот не надо тыкать в кнопку так, будто от этого зависит чья то жизнь",
  "Точно ищем??",
  "Да не зачем так по кнопке долбить!",
  "Да ладно, ладно, сказали искать пошли искать",
  "Так, я забыл, а мы уже ищем?",
  "А вы уверены что мы то что нужно ищем? А то щас что нить найдем, и сами не обрадуемся",
  "Так, я меееедленнно начинаю посик, собираю байты. Ты уже приготовился к результатам?",
  "Да нормальный поиск, не долгий! Нормальный!",
  "Да почти нашли уже!",
  "Нет, я так не могу. Всё, ухожу в отпуск!",
  "Ищите сами, по старинке!!",
  "Я Вам не джин, всякими поисками заниматься",
]

const texts_out = [
  "Вы думаете от меня так легко отделаться?",
  "Это мы уже пробовали",
  "Нет, ну и куда мы теперь пошли? начали искать давайте искать",
  "Так, вот не я это начал!"
]
function* get_direction(){
  let index = 0
  while (true){
    yield texts[index]
    index = texts.length - 1 === index ? 0 : index + 1
  }
}
let generator = get_direction()

function* out_text(){
  let index = 0
  while (true){
    yield texts_out[index]
    index = texts_out.length - 1 === index ? 0 : index + 1
  }
}
let generator_out = out_text()

export default {
  props: ["value"],
  data: ()=> ({
    textDialog: "Вы уверены что хотите начать поиск?",
    dialog_local: false
  }),
  watch: {
    value(nv){
      console.log('trololo')
      this.dialog_local = nv
    }
  },
  methods: {
    async resetDialog() {
      this.dialog_local = false
      new Promise((resolve) => {
        setTimeout(async () => {
          this.textDialog = await generator.next().value
          this.dialog_local = true
          resolve()
        }, 1000)
      })
    },
    outClick() {
      this.dialog_local = false
      new Promise((resolve) => {
        setTimeout(async () => {
          this.textDialog = await generator_out.next().value
          this.dialog_local = true
          resolve()
        }, 1000)
      })
    }
  }
}
</script>