<template>
  <v-card class="funnyback">
    <v-card-text>
      <SearchForm status="status" />
      selected - {{ selected }}
      <p class="subtitle-2 font-italic font-weight-medium">
        Для просмотра связанных с документом сканов раскройте его по кнопке слева в строке - <v-icon>mdi-chevron-down</v-icon>
      </p>
      <Table
          v-model="selected"
          :headers="headers"
          :items="items"
          :status="status"
          :error="error"
          :expanded.sync="expanded"
          show-expand
          item-key="idhkd"
          show-select
          @toggle-select-all="selectItems($event)"
          @item-selected="selectItem($event)"
      >
        <template v-slot:expanded-item="{ headers, item }">
          <td :colspan="headers.length" style="padding: 0 0;">
            <v-data-table
                :value="item.select"
                :headers="headers_docs"
                :items="item.docs"
                @input="item.select = $event"
                show-select
                :hide-default-footer="true"
                style="padding: 0 0 0 56px;"
            >
            </v-data-table>
          </td>
        </template>
      </Table>
    </v-card-text>
    <ActionMenu :selected.sync="selected"/>
  </v-card>
</template>
<script>
  import { mapState, mapActions } from "vuex" // , mapGetters,
  import Table from "../components/common/Table"
  import SearchForm from "../components/Contracts/SearchForm"
  import ActionMenu from "../components/Contracts/ActionMenu"

  export default {
    name: "ContractsView",
    components: {
      Table,
      SearchForm,
      ActionMenu
    },
    data: () => ({
      expanded: undefined,
      selected: [],
      headers: [
        {
          text: "ID ХКД",
          value: "idhkd",
          align: 'left',
          sortable: false
        },
        {
          text: "ID ПКБ",
          value: "idpkb"
        },
        {
          text: "ID Пристав",
          value: "idpristav"
        },
        {
          text: "Номер договора",
          value: "contract_number"
        },
        {
          text: "ФИО должника",
          value: "fio"
        },
        {
          text: "Цессия",
          value: "cession"
        },
        {
          text: "Партнер",
          value: "partner"
        }
      ],
      headers_docs: [
        {
          text: "ID",
          value: "id"
        },
        {
          text: "Название",
          value: "name"
        }
      ]
    }),
    computed: {
      ...mapState("contracts", ["status", "error", "items"]),
    },
    mounted() {
      const params = {
        idDebt: 12
      }
      Promise.all([
        this.GET_ITEM(params)
      ])
    },
    methods: {
      ...mapActions("contracts", [ "GET_ITEM" ]),
      selectItem({ item, value }){
        item.select = value ? item.docs : []
      },
      selectItems(value){
        this.items.map(item=>{this.selectItem({item, value})})
      },
      test(text) {
        console.log(text)
      }
    }
  }
</script>
